

import XHeader from 'vux/src/components/x-header/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { getStore, setStore } from '../../../config/mUtils'
import { getOrderById, doOrderStatus } from '../../../service/getData'

export default {
components: {
XHeader,
InlineXNumber,
XButton,
Spinner,
CellFormPreview,
Group,
Cell
},
mounted() {
this.$vux.loading.show({
text: '加载中...'
})
getOrderById(this.$route.query.id).then(resolved => {
if(resolved.msgCode == '0000') {
this.obj = resolved.data
console.log(resolved.data)
} else {
this.$nextTick(()=>{
this.$vux.toast.show({
type: 'text',
position: 'middle',
text: resolved.msg
})
})

}
})
setTimeout(()=>{
this.$vux.loading.hide()
},800)
},
methods: {
comment(type,id){
if(type === 'comment') {
this.$router.push({
name: 'Grade',
query: {
id: id
}
})
console.log('评论')
} else if(type === 'cancel') {

doOrderStatus(id, 5, getStore('userId')).then(resolved => {
if(resolved.msgCode == '0000') {
this.$router.push({
name: 'Order'
})
} else {
this.$nextTick(()=>{
this.$vux.toast.show({
type: 'text',
position: 'middle',
text: resolved.msg
})
})
}
console.log('取消订单')
})

} else if(type === 'delete') {

doOrderStatus(id, 6, getStore('userId')).then(resolved => {
if(resolved.msgCode == '0000') {
this.$router.push({
name: 'Order'
})
} else {
this.$nextTick(()=>{
this.$vux.toast.show({
type: 'text',
position: 'middle',
text: resolved.msg
})
})
}
})
console.log('删除')
} else if(type === 'again') {
this.$router.push({
name: 'Commodity',
query: {
id: id
}
});
console.log('再次')
} else if(type === 'signfor') {
doOrderStatus(id, 3,  getStore('userId')).then(resolved => {
if(resolved.msgCode == '0000') {
this.$router.push({
name: 'Order'
})
} else {
this.$nextTick(()=>{
this.$vux.toast.show({
type: 'text',
position: 'middle',
text: resolved.msg
})
})
}
console.log(resolved)
})
}
},
payment(id, priceSum) {
this.$router.push({
name: 'PaymentListItem',
query: {
orderId: id,
priceSum: priceSum
}
})
console.log('确认')
},
},
data() {
return {
title: '订单详情',
obj:{goodsVo:{attachmentCover:'',goodsName:'',shopName:'',postagePrice:0}}
}
}
}



